import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { ModalController, LoadingController } from '@ionic/angular';
import { ApiService } from 'src/app/services/api.service';
import { CommonService } from 'src/app/services/common.service';

@Component({
  selector: 'app-profile',
  templateUrl: './profile.page.html',
  styleUrls: ['./profile.page.scss'],
})
export class ProfilePage implements OnInit {
  submitted = false;
  showForm = false;
  loader;
  role;
  emp = {
    name: null,
    empname: null,
    password: null,
    role: {
      id: null,
      description: null,
    },
  };
  menuItems = [
    {
      icon: 'home',
      name: 'Home',
      routerLink: './home',
    },
  ];
  constructor(
    private api: ApiService,
    private common: CommonService,
    public modalController: ModalController,
    public loadingController: LoadingController,
    private router: Router
  ) {}

  ngOnInit() {
    this.role = JSON.parse(localStorage.getItem('role'));
    this.emp = this.common.getUser();
    this.transformMenu();
  }

  ionViewDidEnter() {
    this.role = JSON.parse(localStorage.getItem('role'));
    this.emp = this.common.getUser();
    if (this.menuItems.length <= 1) {
      this.role = JSON.parse(localStorage.getItem('role'));
      this.emp = this.common.getUser();
      this.transformMenu();
    } else if (this.emp !== null) {
      console.log(this.emp);
      this.router.navigate(['landing']);
    } else if (this.emp === null) {
      localStorage.clear();
      this.router.navigate(['landing']);
    } else {
      localStorage.clear();
      this.router.navigate(['home']);
    }
  }

  ionViewWillLeave() {
    this.menuItems = [
      {
        icon: 'home',
        name: 'Home',
        routerLink: './home',
      },
    ];
    console.log('reset nav');
  }

  logout() {
    localStorage.removeItem('user');
    this.router.navigate(['./home']);
    window.location.reload();
  }
  transformMenu() {
    if (
      JSON.parse(localStorage.getItem('user')) === undefined ||
      JSON.parse(localStorage.getItem('user')) === null
    ) {
      console.log('Guest');
      this.menuItems.splice(
        1,
        0,
        {
          icon: 'apartment',
          name: 'Properties',
          routerLink: './properties',
        },
        {
          icon: 'chat',
          name: 'Contact',
          routerLink: './contact',
        }
      );
    } else if (
      this.emp !== null &&
      this.emp !== undefined &&
      (this.emp.role.id === 'a' || this.emp.role.id === 'l')
    ) {
      console.log('admin and landlord con');
      this.menuItems.splice(
        1,
        0,
        {
          icon: 'account_circle',
          name: 'Profile',
          routerLink: './profile',
        },
        {
          icon: 'people',
          name: 'Tenants',
          routerLink: './tenants',
        },
        {
          icon: 'help_center',
          name: 'Queries',
          routerLink: './queries',
        },
        {
          icon: 'apartment',
          name: 'Properties',
          routerLink: './properties',
        },
        {
          icon: 'power_settings_new',
          name: 'Logout',
          routerLink: '',
        }
      );
    } else if (
      this.emp !== null &&
      this.emp !== undefined &&
      this.emp.role.id === 't'
    ) {
      console.log('tenant con');
      this.menuItems.splice(
        1,
        0,
        {
          icon: 'help_center',
          name: 'Queries',
          routerLink: './queries',
        },
        {
          icon: 'apartment',
          name: 'Properties',
          routerLink: './properties',
        },
        {
          icon: 'chat',
          name: 'Contact',
          routerLink: './contact',
        },
        {
          icon: 'power_settings_new',
          name: 'Logout',
          routerLink: './queries',
        }
      );
    } else {
      console.log('team');
      this.menuItems.splice(
        1,
        0,
        {
          icon: 'account_circle',
          name: 'Profile',
          routerLink: './profile',
        },
        {
          icon: 'help_center',
          name: 'Queries',
          routerLink: './queries',
        },
        {
          icon: 'power_settings_new',
          name: 'Logout',
          routerLink: '',
        }
      );
    }
  }
  checkRoute(route) {
    if (route.name === 'Logout') {
      localStorage.clear();
      this.router.navigate(['./home']);
    } else {
      this.router.navigate([route.routerLink]);
    }
  }

  async presentLoading() {

    await this.loadingController
      .create({
        cssClass: 'my-custom-class',
        message: 'Please wait...',
      })
      .then((res) => {
        this.loader = res;
        res.present();
      });
  }

  updateProfile(profile){
    console.log(profile);
  }
}
